
import {onBeforeMount} from "vue"

export default {
  name: "zxInputNumber",
  emits: ['event'],
  props: {
    item: {type: Object, default: {}},
    formData: {},
  },
  setup(prop, context) {
    onBeforeMount(() => {
      if (!prop.formData[prop.item.prop]) {
        prop.formData[prop.item.prop] = 1
      }
    })
    const change = () => {
      context.emit('event', {
        type: 'change',
        prop: prop.item.prop,
        value: prop.formData[prop.item.prop]
      })
    }
    const focus = () => {
      context.emit('event', {
        type: 'focus',
        prop: prop.item.prop,
        value: prop.formData[prop.item.prop]
      })
    }
    const blur = () => {
      context.emit('event', {
        type: 'blur',
        prop: prop.item.prop,
        value: prop.formData[prop.item.prop]
      })
    }
    return {
      change,
      blur,
      focus
    }
  }
}
