
import {computed} from 'vue'

export default {
  name: "zxTag",
  props: {
    item: {type: Object, default: {}},
    formData: {},
  },
  emits: ['event'],
  setup(prop, context) {
    const options = computed(() => {
      if (prop.item.options instanceof Array) {
        return prop.item.options
      } else {
        let list = prop.item.options.split(',')
        return list.map(item => {
          return {value: item, label: item}
        })
      }
    })
    const close = (item) => {
      context.emit("event", {
        type: 'close',
        prop: prop.item.prop,
        value: item
      })
    }
    const click = (item) => {
      context.emit("event", {
        type: 'click',
        prop: prop.item.prop,
        value: item
      })
    }
    return {
      options,
      close,
      click
    }
  },
}
