
export default {
  emits: ['event'],
  props: {
    item: {type: Object, default: {}},
    formData: {},
  },
  setup(prop, context) {
    const click = () => {
      context.emit('event', {
        type: 'click',
        prop: prop.item.prop,
        value: prop.formData[prop.item.prop]
      })
    }

    return {click}
  },
}
